import {UserType} from "../HW8";

type ActionType = { type: "sort", payload: 'up' | 'down' } | { type: "check", payload: number }

export const homeWorkReducer = (state: UserType[], action: ActionType): UserType[] => { // need to fix any
    switch (action.type) {
        case 'sort': {
            // need to fix
            const resultSort = [...state].sort((a, b) => a.name > b.name ? 1 : -1)
            return action.payload === 'up' ? resultSort : resultSort.reverse()
        }
        case 'check': {
            // need to fix
            return state.filter(fl => fl.age >= action.payload)
        }
        default:
            return state
    }
}